@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.footer {
  font-family: "Roboto", sans-serif;
  margin-top: 5px;
  width: 99.5%;
  background-color: #05241d;
  padding: 20px;
  height: fit-content;
  min-height: 30vh;
}

@media only screen and (min-width: 600px) {
  .footer__grid {
    display: flex;
  }
}

.footer__grid__card {
  color: #ccff99;
  /*color: #56a136;*/
  flex: 1;
  display: flex;
  flex-direction: column;
  float: left;
  padding: 0 20px;
}

@media only screen and (max-width: 500px) {
  .footer__grid__card {
    width: 50%;
  }
}

.footer__grid__card>div {
  display: flex;
  flex-direction: row;
}

.footer__grid__card>div>div {
  margin: 0 40px;
}

#main {
  font-weight: bold;
}

#links {
  color: #ccff99;
  /*color: #317f2a;**/
  margin-top: 2px;
}

#links:hover {
  color: #0a5518;
  cursor: pointer;
}

#iconPanel {
  display: flex;
  flex-direction: row;
}

#iconCircle {
  margin: 10px 10px;
  background-color: #ccff99;
  /*background-color: #317f2a;*/
  width: 35px;
  height: 35px;
  color: black;
  border-radius: 17px;
  padding: 5px;
}

#iconCircle:hover {
  background-color: #0a5518;
}

#iconCircle>a {
  color: black;
}

#button {
  width: 100px;
  height: 30px;
  border-radius: 20px;
  font-size: 1em;
  font-weight: bold;
  /*background-color: #317f2a;*/
  background-color: #ccff99;
  border: none;
  margin: 10px 5px;
}

#button:hover {
  background-color: #0a5518;
  cursor: pointer;
}

/* MOBILE SCREEN */
@media screen and (max-width: 450px) {
  .footer {
    width: 100vw;
    margin-top: 5px;
    min-height: 90vh;
  }

  .footer__grid__card {
    padding: 0 1px;
  }

  .footer__grid__card>div div {
    margin: 0 5px;
  }

  #main {
    font-size: 1em;
  }

  #links {
    font-size: 0.6em;
  }

  #iconPanel {
    margin-top: 5px;
    margin-bottom: 20px;
  }

  #iconCircle {
    margin: 5px 10px;
    background-color: #ccff99;
    /*background-color: #1e8449;*/
    width: 20px;
    height: 20px;
    color: black;
    border-radius: 17px;
    padding: 2px;
  }

  #iconIcon {
    font-size: 1em;
  }

  #button {
    width: 60px;
    height: 20px;
    border-radius: 20px;
    font-size: 0.7em;
    font-weight: bold;
    background-color: #ccff99;
    /*background-color: #1e8449;*/
    border: none;
    margin: 10px 5px;
  }
}