.ContactForm {
  width: 99vw;
  height: fit-content;
  min-height: 80vh;
  margin-left: 5vw;
  font-family: 'Times New Roman', Times, serif;
}

.ContactForm>h1 {
  color: black;
  font-size: 27px;
  font-weight: bold;
}

.ContactForm>#contact-form {
  width: 70%;
}

.ContactForm>#contact-form>div {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}



.ContactForm>#contact-form input {
  width: 500px;
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

.ContactForm>#contact-form input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.ContactForm>#contact-form textarea {
  height: 200px;
  width: 500px;
}

.ContactForm>#contact-form button {
  width: 100px;
  height: 40px;
}